import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import Select from "react-select";
import {
  setmmExportFailedRows,
  setmmExportSuccessRows,
  setUploadLoader,
} from "../../../redux-toolkit/slice/TaskpaneSlice";
import { BeatLoader } from "react-spinners";
import "./MaterialExport.css";
import { setLastUsedColumn } from "../../../redux-toolkit/slice/action";
import { setTimeout } from "core-js";
import { DTX_GRAPHQL_URL } from "../../../../config";

function MaterialExport() {
  const { sideBarOpen, lastUsedColumn } = useSelector((state) => state?.action);
  const { MMmappingData, uploadLoader, mmExportFailedRows, mmExportSuccessRows } = useSelector(
    (state) => state?.taskpane
  );
  const dispatch = useDispatch();
  const [mustHaveField, setMustHaveField] = useState(false);
  const [sheetTypes, setSheetTypes] = useState([]);
  const [selectedSheet, setSelectedSheet] = useState(null);
  const [exportConfig, setExportConfig] = useState([]);
  const [errorHandle, setErrorHandle] = useState(false);
  let failedRows = [];
  let exportedRows = [];

  // this useEffect(empty dependency array) hook contain a anonymus function , which reads
  //  all the sheet names in the current opend excel and stores all sheet names into the setSheetTypes state
  // setSheetTypes=["sheet1","sheet2","sheet3",...]
  useEffect(() => {
    Excel.run(function (context) {
      let sheets = context.workbook.worksheets;
      let sheetNames = [];
      context.load(sheets, "items/name");
      return context.sync().then(function () {
        for (var i = 0; i < sheets?.items?.length; i++) {
          sheetNames.push({ value: sheets?.items[i]?.name, label: sheets?.items[i]?.name });
        }
        setSheetTypes(sheetNames);
      });
    }).catch(function (error) {
      console.log(error);
    });
  }, []);
  const customStyles = {
    control: (base, state) => ({
      ...base,
      borderColor: state.isFocused ? "#383332" : "##383332",
      boxShadow: state.isFocused ? null : null,
      "&:hover": { borderColor: state.isFocused ? null : null },
      minHeight: 28,
      height: 28,
      width: 250,
      padding: "0px 0px",
    }),
    option: (base, state) => ({
      ...base,
      backgroundColor: state.isSelected ? "#FFC467" : "##F8F4F0",
      color: state.isSelected ? "#383332" : "#383332",
      "&:hover": { backgroundColor: "#F8F4F0" },
      minHeight: 18,
      width: 250,
      paddingBottom: 4,
    }),
    dropdownIndicator: (base) => ({
      ...base,
      padding: 4,
    }),
    input: (base, state) => ({
      ...base,
      padding: "0px 0px 0px",
    }),
  };

  // handlesheetChange==>  whenever the current sheet switched , then this function reads heading of each column(row 1)
  // based on the address stored in mapping configuration (MMmappingData) and stores into sheetMapData state
  // sheetMapData =[
  //   {
  //     dtxField: materil_id,
  //       excelField: "Custom Materail Id",
  //       address: A,
  //   }
  // ]
  const handleSheetChange = async (selectedSheet) => {
    setSelectedSheet(selectedSheet);
    const sheetMapData = [];
    let MMmapppingDataArray = Object.entries(MMmappingData);
    for (let i = 0; i < MMmapppingDataArray.length; i++) {
      await Excel.run(async function (context) {
        const sheet = context.workbook.worksheets
          .getItem(String(selectedSheet?.value))
          .getRange(String(MMmapppingDataArray[i][1].address + "1"))
          .load("values");
        await context.sync();
        sheetMapData?.push({
          dtxField: MMmapppingDataArray[i][1].name,
          excelField: sheet?.values[0][0],
          address: MMmapppingDataArray[i][1].address,
        });
      });
    }
    setExportConfig(sheetMapData);
  };
  // uploadData ==> after finding the last used row number of  selectedSheet (not active sheet)
  // andchecking the  availablity of all starred feilds, function iterate from row2(to avoid heading) to last row
  // each iteratiion calls a functon  uploadDataToDtx
  const uploadData = async () => {
    try {
      await Excel.run(async function (context) {
        const sheet = context.workbook.worksheets.getItem(String(selectedSheet?.value));
        const lastRow = sheet.getUsedRange().load("rowCount");
        await context.sync();
        if (
          !MMmappingData.hasOwnProperty(
            "custom_material_id" && "material_name" && "plm_material_id" && "unit_of_material"
          )
        ) {
          setMustHaveField(true);
          setTimeout(() => {
            setMustHaveField(false);
          }, 3000);
        } else {
          const tenantToken = localStorage.getItem("tenantToken");
          dispatch(setUploadLoader(true));
          getLastUsedColumn();
          console.log("started", parseInt(lastRow.rowCount));
          for (let i = 2; i <= parseInt(lastRow.rowCount); i++) {
            await uploadDataToDtx(i, tenantToken);
          }
          console.log("ended");
          dispatch(setLastUsedColumn(null));
          dispatch(setmmExportSuccessRows(exportedRows));
          dispatch(setmmExportFailedRows(failedRows));
          dispatch(setUploadLoader(false));
          setErrorHandle(true);

          console.log(failedRows, exportedRows);
        }
      });
    } catch (error) {
      console.log("Error:", error);
      dispatch(setmmExportSuccessRows(exportedRows));
      dispatch(setmmExportFailedRows(failedRows));
      dispatch(setUploadLoader(false));
    }
  };
  // uploadDataToDtx ==> reads the values in each  cell of given row number (i)  based on the Mapping configaration (MMmappingdata)
  //  and allocates these values into respective fields of mutaion varaibles and then calls the api to mutate
  //  if any rows get faied to export then the row number is stored to reupload the same data
  const uploadDataToDtx = async (i, tenantToken) => {
    try {
      let variables = {};
      await Excel.run(async function (context) {
        const sheet = context.workbook.worksheets.getItem(String(selectedSheet?.value)); // String(MMmappingData?.custom_material_id?.address + i)
        let custom_material_id = MMmappingData?.custom_material_id?.address
          ? sheet.getRange(String(MMmappingData?.custom_material_id?.address + i)).load("values")
          : null;
        let erp_material_id = MMmappingData?.erp_material_id?.address
          ? sheet.getRange(String(MMmappingData?.erp_material_id?.address + i)).load("values")
          : null;
        let gross_weight = MMmappingData?.gross_weight?.address
          ? sheet.getRange(String(MMmappingData?.gross_weight?.address + i)).load("values")
          : null;
        let inventory = MMmappingData?.inventory?.address
          ? sheet.getRange(String(MMmappingData?.inventory?.address + i)).load("values")
          : null;
        let lead_time = MMmappingData?.lead_time?.address
          ? sheet.getRange(String(MMmappingData?.lead_time?.address + i)).load("values")
          : null;
        let material_description = MMmappingData?.material_description?.address
          ? sheet.getRange(String(MMmappingData?.material_description?.address + i)).load("values")
          : null;
        let material_group = MMmappingData?.material_group?.address
          ? sheet.getRange(String(MMmappingData?.material_group?.address + i)).load("values")
          : null;
        let material_name = MMmappingData?.material_name?.address
          ? sheet.getRange(String(MMmappingData?.material_name?.address + i)).load("values")
          : null;
        let others = MMmappingData?.others?.address
          ? sheet.getRange(String(MMmappingData?.others?.address + i)).load("values")
          : null;
        let parent_id = MMmappingData?.parent_id?.address
          ? sheet.getRange(String(MMmappingData?.parent_id?.address + i)).load("values")
          : null;
        let plm_material_id = MMmappingData?.plm_material_id?.address
          ? sheet.getRange(String(MMmappingData?.plm_material_id?.address + i)).load("values")
          : null;
        let plm_record_id = MMmappingData?.plm_record_id?.address
          ? sheet.getRange(String(MMmappingData?.plm_record_id?.address + i)).load("values")
          : null;
        let product_code = MMmappingData?.product_code?.address
          ? sheet.getRange(String(MMmappingData?.product_code?.address + i)).load("values")
          : null;
        let quantity = MMmappingData?.quantity?.address
          ? sheet.getRange(String(MMmappingData?.quantity?.address + i)).load("values")
          : null;
        let unit_cost = MMmappingData?.unit_cost?.address
          ? sheet.getRange(String(MMmappingData?.unit_cost?.address + i)).load("values")
          : null;
        let storage_loc = MMmappingData?.storage_loc?.address
          ? sheet.getRange(String(MMmappingData?.storage_loc?.address + i)).load("values")
          : null;
        let unit_of_material = MMmappingData?.unit_of_material?.address
          ? sheet.getRange(String(MMmappingData?.unit_of_material?.address + i)).load("values")
          : null;
        let unit_sale_price = MMmappingData?.unit_sale_price?.address
          ? sheet.getRange(String(MMmappingData?.unit_sale_price?.address + i)).load("values")
          : null;
        let weight_unit = MMmappingData?.weight_unit?.address
          ? sheet.getRange(String(MMmappingData?.weight_unit?.address + i)).load("values")
          : null;
        await context.sync();
        variables = {
          custom_material_id: String(custom_material_id?.values[0][0]),
          erp_material_id: erp_material_id?.values[0][0],
          gross_weight: gross_weight?.values[0][0],
          inventory: inventory?.values[0][0],
          lead_time: lead_time?.values[0][0],
          material_description: material_description?.values[0][0],
          material_group: material_group?.values[0][0],
          material_name: material_name?.values[0][0],
          others: others?.values[0][0],
          parent_id: parent_id?.values[0][0],
          plm_material_id: String(plm_material_id?.values[0][0]),
          plm_record_id: plm_record_id?.values[0][0],
          product_code: product_code?.values[0][0],
          quantity: quantity?.values[0][0],
          unit_cost: unit_cost?.values[0][0],
          storage_loc: storage_loc?.values[0][0],
          unit_of_material: unit_of_material?.values[0][0],
          unit_sale_price: unit_sale_price?.values[0][0],
          weight_unit: weight_unit?.values[0][0],
        };
        const body = {
          operationName: "CreateMaterialMaster",
          variables: variables,
          query:
            "mutation CreateMaterialMaster($custom_material_id: String, $erp_material_id: String, $gross_weight: Int, $inventory: Int, $lead_time: Int, $material_description: String, $material_group: Int, $material_name: String, $others: jsonb, $parent_id: Int, $plm_material_id: String, $plm_record_id: String, $product_code: Int, $quantity: float8, $storage_loc: Int, $unit_cost: Int, $unit_of_material: String, $unit_sale_price: Int, $weight_unit: String) {\n  insert_core_material_master_one(\n    object: {custom_material_id: $custom_material_id, erp_material_id: $erp_material_id, gross_weight: $gross_weight, inventory: $inventory, lead_time: $lead_time, material_description: $material_description, material_group: $material_group, material_name: $material_name, others: $others, parent_id: $parent_id, plm_material_id: $plm_material_id, plm_record_id: $plm_record_id, product_code: $product_code, quantity: $quantity, unit_cost: $unit_cost, storage_loc: $storage_loc, unit_of_material: $unit_of_material, unit_sale_price: $unit_sale_price, weight_unit: $weight_unit}\n  ) {\n    id\n    __typename\n  }\n}\n",
        };
        await fetch(DTX_GRAPHQL_URL, {
          method: "POST",
          headers: {
            "Content-Type": "application/json",
            Authorization: "Bearer " + tenantToken,
          },
          body: JSON.stringify(body),
        })
          .then((response) => response.json())
          .then((data) => {
            console.log(data);
            if (data.data) {
              markSuccess(i);
              exportedRows.push(i);
            } else {
              console.log(i, "error happened");
              markFailed(i);
              failedRows.push(i);
            }
          })
          .catch((error) => {
            console.log("Error", error);
            markFailed(i);
            failedRows.push(i);
          });
      });
    } catch (error) {
      console.log("Error:", error);
    }
  };
  // getLastUsedColumn=> it stores last used column name into lastusedcolumnName state
  const getLastUsedColumn = async () => {
    console.log("getLastUsedColumn", selectedSheet?.value);
    await Excel.run(async function (context) {
      const sheet = context.workbook.worksheets.getItem(String(selectedSheet?.value));
      const lastRow = sheet.getUsedRange().load("address");
      await context.sync();
      dispatch(setLastUsedColumn(lastRow?.address?.split(":")[1]?.split("")[0]));
    });
  };
  //mark success will fill the  green color to the given row
  const markSuccess = async (i) => {
    console.log("inside success");
    if (lastUsedColumn) {
      await Excel.run(async function (context) {
        context.workbook.worksheets
          .getItem(String(selectedSheet?.value))
          .getRange(String(`A${i}:${lastUsedColumn + i}`)).format.fill.color = "#3eea4a";
      });
    } else {
      await Excel.run(async function (context) {
        const sheet = context.workbook.worksheets.getItem(String(selectedSheet?.value));
        const lastRow = sheet.getUsedRange().load("address");
        return context.sync().then(() => {
          context.workbook.worksheets
            .getItem(String(selectedSheet?.value))
            .getRange(String(`A${i}:${lastRow?.address?.split(":")[1]?.split("")[0] + i}`)).format.fill.color =
            "#3eea4a";
        });
      });
    }
  };
  //markFailed ==> will fill the color red to given fields
  const markFailed = async (i) => {
    if (lastUsedColumn) {
      await Excel.run(async function (context) {
        context.workbook.worksheets
          .getItem(String(selectedSheet?.value))
          .getRange(String(`A${i}:${lastUsedColumn + i}`)).format.fill.color = "#d03758";
      });
    } else {
      await Excel.run(async function (context) {
        const sheet = context.workbook.worksheets.getItem(String(selectedSheet?.value));
        const lastRow = sheet.getUsedRange().load("address");
        return context.sync().then(() => {
          context.workbook.worksheets
            .getItem(String(selectedSheet?.value))
            .getRange(String(`A${i}:${lastRow?.address?.split(":")[1]?.split("")[0] + i}`)).format.fill.color =
            "#d03758";
        });
      });
    }
  };
  //reuploadErrorOccuredData ==> will upload the rows which failed on last export
  const reuploadErrorOccuredData = async () => {
    let tenantToken = localStorage.getItem("tenantToken");
    dispatch(setUploadLoader(true));
    failedRows = [];
    exportedRows = [];
    for (let i = 0; i < mmExportFailedRows.length; i++) {
      await uploadDataToDtx(parseInt(mmExportFailedRows[i]), tenantToken);
    }
    dispatch(setUploadLoader(false));
    dispatch(setmmExportSuccessRows(exportedRows));
    dispatch(setmmExportFailedRows(failedRows));
    console.log(failedRows, exportedRows);
  };
  //cancelReupload ==> fucntion wchich cancels the option to  reupload and ready for next export
  const cancelReupload = () => {
    setErrorHandle(false);
  };

  return (
    <>
      <div className="mm-export-container">
        <div>
          <p htmlFor="">Selelct the sheet name </p>
          <Select
            options={sheetTypes}
            styles={customStyles}
            onChange={handleSheetChange}
            value={selectedSheet}
            className={`${sideBarOpen ? "selectType" : " "}`}
            isDisabled={uploadLoader ? true : false}
          />
        </div>

        {MMmappingData ? (
          exportConfig?.length > 0 && (
            <table className="mm-export-table">
              <thead>
                <tr>
                  <th>Excel Column Name </th>
                  <th>DTX Column Name </th>
                  <th>Address</th>
                </tr>
              </thead>
              <tbody>
                {exportConfig?.map((obj) => (
                  <tr>
                    <td> {obj?.excelField}</td>
                    <td>{obj.dtxField}</td>
                    <td>{obj.address}</td>
                  </tr>
                ))}
              </tbody>
            </table>
          )
        ) : (
          <p>you dont have any mapping configuration data. Please map before exporting</p>
        )}
        {mustHaveField && (
          <p className="warning-mustHaveFields">
            check the mapping configuration data, should contain all starred fields{" "}
          </p>
        )}
        {exportConfig?.length > 0 && !errorHandle && (
          <div className="upload">
            {!uploadLoader ? (
              <button
                className="export-material-matser"
                onClick={() => {
                  uploadData();
                }}
              >
                Export
              </button>
            ) : (
              <button>
                <BeatLoader color="#000000" size={5} />
              </button>
            )}
          </div>
        )}
        {errorHandle && (
          <div className="upload after-export">
            <div>
              <p>
                <span className="successNo">{mmExportSuccessRows?.length}</span> Row(s) exported successfully
              </p>

              <p>
                <span className="failedNo">{mmExportFailedRows?.length}</span> Row(s) failed to export
              </p>
            </div>
            <div className="after-export-buttons">
              <button className="cancel" onClick={cancelReupload}>
                close
              </button>
              {mmExportFailedRows.length !== 0 && (
                <button className="reUpload" onClick={!uploadLoader && reuploadErrorOccuredData}>
                  {uploadLoader ? <BeatLoader color="#000000" size={5} /> : "Reupload"}
                </button>
              )}
            </div>
          </div>
        )}
      </div>
    </>
  );
}

export default MaterialExport;
