import React from "react";
import { useSelector } from "react-redux";
import BeaconHeader from "../../commonComponents/BeaconHeader/BeaconHeader";
import MaterialExport from "../../commonComponents/Material/MaterialExport/MaterialExport";
import MaterialGet from "../../commonComponents/Material/MaterialGet/MaterialGet";
import MaterialMap from "../../commonComponents/Material/MaterialMap/MaterialMap";
import NavBar from "../../commonComponents/Navbar/NavBar";
import SideBarMenu from "../../commonComponents/SideBarMenu/SideBarMenu";
import "./MM.css";

function MM() {
  const { materialAction } = useSelector((state) => state?.action);
  return (
    <>
      <div className="commonContainer">
        <div className="commonNavBar">
          <NavBar />
        </div>
        <div className="commonBody ">
          <div className="commonSidebarBox">
            <SideBarMenu />
          </div>
          <div className="commonActivityArea">

{(materialAction==="get")?<MaterialGet/> :null}
{(materialAction==="map")?<MaterialMap/> :null}
{(materialAction==="export")?<MaterialExport/>:null}
{(materialAction==="update")?<MaterialGet/>:null}
 
 </div>
        </div>
      </div>
    </>
  );
}

export default MM;
