import React, { useState } from "react";
import Select from "react-select";
import { useDispatch, useSelector } from "react-redux";
import { setMMmappingData, setMMmappingData2 } from "../../../redux-toolkit/slice/TaskpaneSlice";
import { BeatLoader } from "react-spinners";
import { RiEditBoxLine } from "react-icons/ri";
import { IoCloseCircleOutline } from "react-icons/io5";
import "./MaterialMap.css";

function MaterialMap() {
  const { MMmappingData, uploadLoader } = useSelector((state) => state.taskpane);
  const { sideBarOpen } = useSelector((state) => state.action);
  const [rangeFor, setRangeFor] = useState({});
  const [edit, setEdit] = useState(false);
  const [mustHaveField, setMustHaveField] = useState();
  const dispatch = useDispatch();
  const fieldFor = [
    { label: "Material Id* ", value: "custom_material_id" },
    { label: "ERP Id ", value: "erp_material_id" },
    { label: "Gross Weight", value: "gross_weight" },
    { label: "Inventory", value: "inventory" },
    { label: "Lead Time", value: "lead_time" },
    { label: "Others", value: "material_description" },
    { label: "Material Group", value: "material_group" },
    { label: "Material Name* ", value: "material_name" },
    { label: "Material Product Code ", value: "material_product_code" },
    { label: "Material Type ", value: "material_type" },
    // { label: "Others", value: "others" },
    { label: "Parent Id ", value: "parent_id" },
    { label: "PLM Id* ", value: "plm_material_id" },
    { label: "PLM Record Id", value: "plm_record_id" },
    { label: "Product Code ", value: "product_code" },
    { label: "Quantity", value: "quantity" },
    { label: "Status", value: "status" },
    { label: "Stogae", value: "storage_loc" },
    { label: "Unit Cost", value: "unit_cost" },
    { label: "UOM* ", value: "unit_of_material" },
    { label: "Unit Sale Price", value: "unit_sale_price" },
    { label: "Weight Unit", value: "weight_unit" },
  ];
  const customStyles = {
    control: (base, state) => ({
      ...base,
      borderColor: state.isFocused ? "#383332" : "##383332",
      boxShadow: state.isFocused ? null : null,
      "&:hover": { borderColor: state.isFocused ? null : null },
      minHeight: 28,
      height: 28,
      width: 250,
      padding: "0px 0px",
    }),
    option: (base, state) => ({
      ...base,
      backgroundColor: state.isSelected ? "#FFC467" : "##F8F4F0",
      color: state.isSelected ? "#383332" : "#383332",
      "&:hover": { backgroundColor: "#F8F4F0" },
      minHeight: 18,
      width: 250,
      paddingBottom: 4,
    }),
    dropdownIndicator: (base) => ({
      ...base,
      padding: 4,
    }),
    input: (base, state) => ({
      ...base,
      padding: "0px 0px 0px",
    }),
  };
  const selectRangeFor = (rangeFor) => {
    setRangeFor(rangeFor);
  };

  //function  reserveSelectedSpace  which stores selected address into MMmapping data (state in redux)
  // you have to select what kind of region you are looking for ,if we want select entire colummn then need to select "column" from range type
  // if first column of the excel has selected the the address by default  "A:A" or "A1:Ax" or (if you selected a region like "A10:C20") will be changed to "A"
  //MMmappingData is an object will store the selected address
  // MMmappingData={
  // custom_material_id:{ name: "Custom Material Id* ", address: "A" }
  //}
  const reserveSelectedSpace = async () => {
    try {
      rangeFor &&
        (await Excel.run(async (context) => {
          const range = context.workbook.getSelectedRange();
          range.load("address");
          await context.sync();
          let rangeAddress = range.address.split("!");
          let selectedAddress = String(rangeAddress[1]).substring(0, 1);
          dispatch(setMMmappingData({ name: rangeFor.label, value: rangeFor.value, address: selectedAddress }));
          context.workbook.worksheets.getActiveWorksheet().getRange(String(selectedAddress + 1)).format.fill.color =
            "#FFF0D9";
          return context.sync();
        }));
    } catch (error) {
      console.log("Error:", error);
    }
  };

  const saveMMmappingConfig = () => {};
  // deleteCurrentRow == > deletes the selected row from mapping data
  const deleteCurrentRow = (key) => {
    Excel.run((context) => {
      context.workbook.worksheets
        .getActiveWorksheet()
        .getRange(String(MMmappingData[key].address + 1))
        .format.fill.clear("color");
      return context.sync();
    });
    const MMconfigDataCopy = { ...MMmappingData };
    delete MMconfigDataCopy[key];
    dispatch(setMMmappingData2(MMconfigDataCopy));
  };
  return (
    <body className="ms-font-m ms-welcome ms-Fabric">
      <div id="app-body" className="app-body">
        <div className="select-map-feild">
          <p htmlFor="">Selelct the field</p>
          <Select
            options={fieldFor}
            styles={customStyles}
            onChange={selectRangeFor}
            value={rangeFor}
            className={`${sideBarOpen ? "selectType" : " "}`}
          />

          <div className="action">
            <p className="warning" id="warning"></p>
          </div>
          <div className="action">
            <button className="apply" id="run" onClick={reserveSelectedSpace}>
              Apply
            </button>
          </div>

          <table id="configure-table">
            <thead>
              <tr>
                <th>Mapping to </th>
                <th>Address </th>
                <th onClick={() => setEdit(!edit)}>
                  <RiEditBoxLine />
                </th>
              </tr>
            </thead>
            <tbody>
              {Object.entries(MMmappingData)?.map(
                ([key, value]) =>
                  value?.address && (
                    <tr>
                      <td>
                        {value.name}
                        {key === "custom_material_id" ||
                        key === "material_name" ||
                        key === "plm_material_id" ||
                        key === "unit_of_material" ? (
                          <sup>*</sup>
                        ) : null}
                      </td>
                      <td>{value.address}</td>
                      {edit && (
                        <td
                          onClick={() => {
                            deleteCurrentRow(key);
                          }}
                        >
                          <IoCloseCircleOutline />
                        </td>
                      )}
                    </tr>
                  )
              )}
            </tbody>
          </table>
          {mustHaveField && (
            <p className="warnings" id="warnings">
              You have to map all starred regions to complete the data export
            </p>
          )}
          <div className="upload">
            {!uploadLoader ? (
              <button
                onClick={() => {
                  saveMMmappingConfig();
                }}
              >
                Save
              </button>
            ) : (
              <button>
                <BeatLoader color="#000000" size={5} />
              </button>
            )}
          </div>
        </div>
      </div>
    </body>
  );
}

export default MaterialMap;
